@keyframes fadeIn {
  0% {
    opacity: 0;
  }
  50% {
    opacity: 0.5;
  }
  100% {
    opacity: 1;
  }
}

@keyframes imgani {
  0% {
    transform: scale(0);
  }
  100% {
    transform: scale(1.1);
  }
}
.movie {
  background-color: white;
  margin-bottom: 70px;
  font-weight: 300;
  padding: 20px;
  border-radius: 5px;
  color: #adaeb9;
  display: grid;
  grid-template-columns: minmax(150px, 1fr) 2fr;
  grid-gap: 20px;
  text-decoration: none;
  color: inherit;
  box-shadow: 12px 25px 2px 1px rgba(0, 0, 255, 0.2);
  animation: fadeIn 1.2s ease-in-out;
}

.movie__img {
  position: relative;
  top: 0px;
  max-width: 150px;
  width: 100%;
  margin-right: 10px;
  border-radius: 10px;
  box-shadow: 0 30px 60px -12px rgba(28, 28, 29, 0.25),
    0 18px 36px -18px rgba(0, 0, 0, 0.3), 0 -2px 36px -8px rgba(1, 1, 1, 0.025);
  cursor: pointer;
  animation: imgani 2s ease-in-out;
}

.movie__title,
.movie__year {
  margin-top: 10px;
  font-weight: 600;
  text-decoration: none;
}

.movie__title a {
  margin-bottom: 5px;
  font-size: 24px;
  color: #2c2c2c;
  text-decoration: none;
}

.movie__genres {
  list-style: none;
  padding: 0;
  margin: 0;
  display: flex;
  flex-wrap: wrap;
  margin: 0 10px;
  cursor: pointer;
}

.movie__genres :hover {
  color: greenyellow;
  font-weight: 600;
}

.movie__genres li {
  margin-right: 10px;
  font-size: 14px;
}

.movie__summary {
  margin-bottom: 30px;
}
